import axios from "axios";
import { useMutation, useQuery, useQueryClient } from "react-query";
export function useServiceInfo() {
  return useQuery(
    "service-info",
    async () => {
      const data = await axios.get("/microservices").then(({ data }) => data);
      return data;
    },
    {
      staleTime: 60000,
    }
  );
}
